import Geomotry from "./geometry";
import StorageInstance from '../storage.js';

class Custom extends Geomotry {

    constructor(file) {
        super();
        
        this.vertices = file.position.array;
        this.uvs = file.uv.array;
        this.normals = file.normal.array;
        this.indices = new Uint16Array(file.index.array);

        this.generateBuffer(this.vertices, this.uvs, this.indices, this.normals);

    }
}
export default Custom;


