import '../scss/app.scss'
import SceneManager from './sceneManager.js'
import StorageInstance from './storage.js'

console.log('Saint 🔥');
document.addEventListener('DOMContentLoaded', init, false);

function init() {

  let canvasComponent = document.querySelectorAll('.canvas');
  let canvas = [];

  for (let i = 0; i < canvasComponent.length; i++){
    canvas[i] = new SceneManager(canvasComponent[i]);
  }
}




