import BasicMaterial from '../materials/basicMaterial';
import Custom from '../bufferGeometries/custom';
import { mat4, quat, vec3 } from 'gl-matrix';
import StorageInstance from '../storage.js';
import Loaders from '../utils/loader';

class Monolithe {

    constructor() {

        this.material   = new BasicMaterial();
        this.material.setColor(vec3.fromValues(150, 0, 0));

        this.initGeo = this.initGeo.bind(this);

        if(!StorageInstance.cache.roseGeometry){
            Promise.all([
                Loaders.loadJson('../assets/objects/rose.json')
            ]).then(this.initGeo);
        }else{
            this.copyGeo();
        }

    }

    initGeo(resources){
        this.geometry = new Custom(JSON.parse(resources[0]));
        StorageInstance.cache.roseGeometry = this.geometry;
        this.MVP = mat4.create();

        this.geometry.z = -1;
        this.geometry.x = 1;

        this.init = true;
    }

    copyGeo(){
        this.geometry = StorageInstance.cache.roseGeometry;
        
        this.MVP = mat4.create();

        this.geometry.z = -1;
        this.geometry.x = 1;

        this.init = true;
    }

    render() {

        this.geometry.updatePositionMatrix();
        let gl = StorageInstance.gl;
        let program = this.material.program;

        gl.useProgram(program);

        gl.bindBuffer(gl.ARRAY_BUFFER, this.geometry.InformationsBuffer);

        gl.enableVertexAttribArray(this.material.positionAttribLoc);
        gl.vertexAttribPointer(this.material.positionAttribLoc, 3, gl.FLOAT, false, 32, 0);

        gl.enableVertexAttribArray(this.material.uvAttribLoc);
        gl.vertexAttribPointer(this.material.uvAttribLoc, 2, gl.FLOAT, false, 32, 12);

        StorageInstance.camera.updateViewMatrix();
        this.MVP = StorageInstance.camera.getMVP(this.geometry.modelMatrix);

        gl.uniform2fv(this.material.resolutionUnifLoc, StorageInstance.resolution);
        gl.uniformMatrix4fv(this.material.modelMVPLoc, false, this.MVP);
        gl.uniform3fv(this.material.colorLoc, this.material.color);
        gl.uniform1f(this.material.timeUnifLoc, StorageInstance.time);

        gl.bindBuffer(gl.ELEMENT_ARRAY_BUFFER, this.geometry.indicesBuffer);
        gl.drawElements(gl.TRIANGLES, this.geometry.indices.length, gl.UNSIGNED_SHORT, 0);

    }
}
export default Monolithe;


