import Loader from '../utils/loader.js';
import vertexSource from '../../shaders/monolithe.vert';
import fragmentSource from '../../shaders/monolithe.frag';
import StorageInstance from '../storage.js';
import { vec3 } from 'gl-matrix';


class MonolitheMaterial {

    constructor() {
        this.program;
        this.color = vec3.create();
        this.texture = StorageInstance.gl.createTexture();
        this.rangeColor = vec3.fromValues(255, 255, 255);

        this.initProgram();
    }

    setColor(v) {
        this.color = v;
        vec3.divide(this.color, this.color, this.rangeColor);
    }

    setTexture(text){
        let gl = StorageInstance.gl;
        
        gl.bindTexture(gl.TEXTURE_2D, this.texture);

        gl.texParameteri(gl.TEXTURE_2D, gl.TEXTURE_WRAP_S, gl.REPEAT)
        gl.texParameteri(gl.TEXTURE_2D, gl.TEXTURE_WRAP_T, gl.REPEAT)
        gl.texParameteri(gl.TEXTURE_2D, gl.TEXTURE_MIN_FILTER, gl.LINEAR)
        gl.texParameteri(gl.TEXTURE_2D, gl.TEXTURE_MAG_FILTER, gl.NEAREST)

        gl.texImage2D(gl.TEXTURE_2D, 0, gl.RGBA, gl.RGBA, gl.UNSIGNED_BYTE, text)
    }

    initProgram(){

        let gl = StorageInstance.gl;

        let vShader = gl.createShader(gl.VERTEX_SHADER);
        this.vShader = vShader;
        gl.shaderSource(vShader, vertexSource);
        gl.compileShader(vShader);

        if (!gl.getShaderParameter(vShader, gl.COMPILE_STATUS)) {
            console.error('error vert', gl.getShaderInfoLog(vShader));
        }

        let fShader = gl.createShader(gl.FRAGMENT_SHADER);
        this.fShader = fShader;
        gl.shaderSource(fShader, fragmentSource);
        gl.compileShader(fShader);

        if (!gl.getShaderParameter(fShader, gl.COMPILE_STATUS)) {
            console.error('error frag', gl.getShaderInfoLog(fShader));
        }

        let program = gl.createProgram()
        gl.attachShader(program, vShader);
        gl.attachShader(program, fShader);
        gl.linkProgram(program);

        if (!gl.getProgramParameter(program, gl.LINK_STATUS)) {
            console.error(gl.getProgramInfoLog(program));
        }

        gl.useProgram(program);

        // Uniforms Locations
        this.modelMVPLoc = gl.getUniformLocation(program, "uMVP");
        this.modelLoc = gl.getUniformLocation(program, "uModel");
        this.colorLoc = gl.getUniformLocation(program, "uColor");
        this.lightSourceLoc = gl.getUniformLocation(program, "uLightSource");
        this.secondLightSourceLoc = gl.getUniformLocation(program, "uSecondLightSource");
        this.lightColorLoc = gl.getUniformLocation(program, "uLightColor");
        this.secondLightColorLoc = gl.getUniformLocation(program, "uSecondLightColor");

        // Attributes Locations
        this.positionAttribLoc = gl.getAttribLocation(program, 'aPos');
        this.uvAttribLoc       = gl.getAttribLocation(program, 'aUv');
        this.normalAttribLoc   = gl.getAttribLocation(program, 'aNormal');

        this.program = program
    }
}
export default MonolitheMaterial;


