import * as glMatrix from 'gl-matrix';

const mat4 = glMatrix.mat4;
const quat = glMatrix.quat;
const vec3 = glMatrix.vec3;

const UP = vec3.fromValues(0, 1, 0)
const M4 = mat4.create()

class Camera {

    constructor(opts = { near: 1, far: 100, fov: 60, aspect: window.innerWidth / window.innerHeight }) {

        this.near = opts.near;
        this.far = opts.far;
        this.fov = opts.fov;
        this.aspect = opts.aspect;

        this.projectionMatrix = mat4.create();
        this.viewMatrix = mat4.create();

        this.position = vec3.create();

        this.updateProjection(this.aspect);
    }

    updateViewMatrix() {

        mat4.identity(this.viewMatrix, this.viewMatrix);
        mat4.translate(this.viewMatrix, this.viewMatrix, this.position);
    }

    getMVP(modelMatrix) {

        mat4.mul(M4, this.viewMatrix, modelMatrix)
        mat4.mul(M4, this.projectionMatrix, M4)

        return M4

    }

    updateProjection(aspect) {

        this.aspect = aspect

        // mat4.perspective( this.projectionMatrix, 60 * ( Math.PI/180 ), 400 / 400, 0.1, 100.0 )

        mat4.perspective(this.projectionMatrix,
            this.fov * (Math.PI / 180),
            this.aspect,
            this.near,
            this.far
        )

    }

    lookAt(target) {

        mat4.identity(this.viewMatrix);

        mat4.lookAt(
            this.viewMatrix,
            this.position,
            target,
            UP
        )

    }

}

export default Camera


