import GeomotryInformations from "./planeInformations";
import Geomotry from "./geometry";
import StorageInstance from '../storage.js';

class Plane extends Geomotry {

    constructor() {
        super();
        
        this.vertices = GeomotryInformations.vertices;
        this.uvs = GeomotryInformations.uvs;
        this.indices = new Uint16Array(GeomotryInformations.indices);

        this.generateBuffer(this.vertices, this.uvs, this.indices);  
    }
}
export default Plane;


