import BasicMaterial from '../materials/backgroundMaterial';
import Plane from '../bufferGeometries/plane';
import {mat4, quat, vec3} from 'gl-matrix';
import StorageInstance from '../storage.js'

class Background {

    constructor() {

        this.material    = new BasicMaterial();
        this.geometry    = new Plane();
        this.MVP         = mat4.create();

        this.firstColor = vec3.create();
        this.secondColor = vec3.create();
        this.rangeColor = vec3.fromValues(255, 255, 255);

        this.setFirstColor(vec3.fromValues(51, 21, 60));
        this.setSecondColor(vec3.fromValues(22, 64, 42));

        this.init = true;
    }

    render() {
        let gl = StorageInstance.gl;
        let program = this.material.program;

        gl.useProgram(program);

        gl.bindBuffer(gl.ARRAY_BUFFER, this.geometry.InformationsBuffer);

        gl.enableVertexAttribArray(this.material.positionAttribLoc);
        gl.vertexAttribPointer(this.material.positionAttribLoc, 3, gl.FLOAT, false, 20, 0);

        gl.enableVertexAttribArray(this.material.uvAttribLoc);
        gl.vertexAttribPointer(this.material.uvAttribLoc, 2, gl.FLOAT, false, 20, 12);

        gl.uniform2fv(this.material.resolutionUnifLoc, StorageInstance.resolution);
        gl.uniformMatrix4fv(this.material.modelMVPLoc, false, this.MVP);
        gl.uniform1f(this.material.timeUnifLoc, StorageInstance.time);

        gl.uniform3fv(this.material.firstColorUnifLoc, this.firstColor);
        gl.uniform3fv(this.material.secondColorUnifLoc, this.secondColor);

        gl.bindBuffer(gl.ELEMENT_ARRAY_BUFFER, this.geometry.indicesBuffer);
        gl.drawElements(gl.TRIANGLES, this.geometry.indices.length, gl.UNSIGNED_SHORT, 0);
        gl.clear(gl.DEPTH_BUFFER_BIT);

    }


    setFirstColor(v) { 
        this.firstColor = v; 
        vec3.divide(this.firstColor, this.firstColor, this.rangeColor);
    }

    setSecondColor(v){
        this.secondColor = v;
        vec3.divide(this.secondColor, this.secondColor, this.rangeColor);
    }

}
export default Background;


