import MonolitheMaterial from '../materials/monolitheMaterial';
import Custom from '../bufferGeometries/custom';
import { mat4, quat, vec3 } from 'gl-matrix';
import StorageInstance from '../storage.js';
import Loaders from '../utils/loader';
import Texture from './texture';

class Monolithe {

    constructor() {

        this.material = new MonolitheMaterial();
        this.texture = new Texture();
        this.material.setColor(vec3.fromValues(1, 1, 1));
        this.material.setTexture(this.texture.canvas);

        this.initGeo = this.initGeo.bind(this);

        Promise.all([
            Loaders.loadJson('../assets/objects/monolithe.json')
        ]).then(this.initGeo);

    }

    initGeo(resources){
        this.geometry = new Custom(JSON.parse(resources[0]));
        this.MVP = mat4.create();

        this.geometry.z = -6;
        this.geometry.rotateY(Math.PI);

        this.place();

        this.init = true;
    }

    place() {

        let positions = [];
        let informations = this.geometry.informations;

        this.geometry.updatePositionMatrix();
        StorageInstance.camera.updateViewMatrix();
        this.MVP = StorageInstance.camera.getMVP(this.geometry.modelMatrix);

        for (let i = 0; i < informations.length; i += 8) {
            let pos = vec3.fromValues(informations[i], informations[i + 1], informations[i + 2])
            vec3.transformMat4(pos, pos, this.MVP);
            positions.push(pos);
        }
        
        let nearX, fearX, nearY, fearY;
        for (let i = 0; i < positions.length; i++) {
            if (positions[i][0] < nearX || nearX == null) nearX = positions[i][0];
            if (positions[i][0] > fearX || fearX == null) fearX = positions[i][0];
        }

        for (let i = 0; i < positions.length; i++) {
            if (positions[i][0] == nearX || positions[i][0] == fearX){
                if (positions[i][1] < nearY || nearY == null) nearY = positions[i][1];
                if (positions[i][1] > fearY || fearY == null) fearY = positions[i][1];
            }
        }

        this.geometry.scale[1] = 0.8 / fearY ; 
        this.geometry.scale[0] = 0.25 / fearX;
        
        let transform = vec3.fromValues(0, ((nearY + 0.2) * this.geometry.scale[1]), positions[Math.floor(positions.length / 2)][2]);
        let inversedMatrix = mat4.create();
        mat4.invert(inversedMatrix, this.MVP);
        vec3.transformMat4(transform, transform, inversedMatrix );
        console.log('tan', Math.tan((StorageInstance.camera.fov / 2) * StorageInstance.camera.aspect  * (Math.PI / 180)) * 6);
        this.geometry.x = -1 * (Math.tan((StorageInstance.camera.fov / 2) * StorageInstance.camera.aspect * (Math.PI / 180)) * 6) * 0.35;
        this.geometry.y = this.geometry.y - transform[1];
        this.geometry.needUpdate = true;
        this.geometry.updatePositionMatrix();
    }

    render() {

        if (this.texture.textureUpdated == true && this.texture.readyToUse == true){
            this.material.setTexture(this.texture.canvas);
            this.texture.textureUpdated = false;
        }
        this.geometry.updatePositionMatrix();
        let gl = StorageInstance.gl;
        let lights = StorageInstance.lights;
        let program = this.material.program;
        gl.useProgram(program);

        gl.bindBuffer(gl.ARRAY_BUFFER, this.geometry.InformationsBuffer);

        gl.enableVertexAttribArray(this.material.positionAttribLoc);
        gl.vertexAttribPointer(this.material.positionAttribLoc, 3, gl.FLOAT, false, 32, 0);

        gl.enableVertexAttribArray(this.material.uvAttribLoc);
        gl.vertexAttribPointer(this.material.uvAttribLoc, 2, gl.FLOAT, false, 32, 12);

        gl.enableVertexAttribArray(this.material.normalAttribLoc);
        gl.vertexAttribPointer(this.material.normalAttribLoc, 3, gl.FLOAT, false, 32, 20);
        
        StorageInstance.camera.updateViewMatrix();
        this.MVP = StorageInstance.camera.getMVP(this.geometry.modelMatrix);
        
        let adjustedLD = vec3.create();
        vec3.normalize(adjustedLD, lights[0].position, lights[0].position);
        vec3.scale(adjustedLD, adjustedLD, -1); 

        let secondAdjustedLD = vec3.create();
        vec3.normalize(secondAdjustedLD, lights[1].position, lights[1].position);
        vec3.scale(secondAdjustedLD, secondAdjustedLD, -1); 

        gl.uniform2fv(this.material.resolutionUnifLoc, StorageInstance.resolution);
        gl.uniform3fv(this.material.lightSourceLoc, adjustedLD);
        gl.uniform3fv(this.material.lightColorLoc, lights[0].color);
        gl.uniform3fv(this.material.secondLightSourceLoc, secondAdjustedLD);
        gl.uniform3fv(this.material.secondLightColorLoc, lights[1].color);
        gl.uniformMatrix4fv(this.material.modelMVPLoc, false, this.MVP);
        gl.uniformMatrix4fv(this.material.modelLoc, false, this.geometry.modelMatrix);
        gl.uniform3fv(this.material.colorLoc, this.material.color);
        gl.uniform1f(this.material.timeUnifLoc, StorageInstance.time);

        gl.bindBuffer(gl.ELEMENT_ARRAY_BUFFER, this.geometry.indicesBuffer);
        gl.drawElements(gl.TRIANGLES, this.geometry.indices.length, gl.UNSIGNED_SHORT, 0);

    }
}
export default Monolithe;


