import { vec3, quat, mat4 } from "gl-matrix";
import StorageInstance from '../storage.js';

class Geometry {

    constructor() {
        this.position    = vec3.fromValues(0, 0, 0);
        this.scale       = vec3.fromValues(1, 1, 1);
        this.rotation    = vec3.fromValues(0, 0, 0);
        this.quaternion  = quat.create();
        this.modelMatrix = mat4.create();
        this.needUpdate  = true;
    }

    generateBuffer(vertices, uvs, indices, normals){
        let gl = StorageInstance.gl;

        this.informations = this.generateInformations(vertices, uvs, normals);

        this.indicesBuffer = gl.createBuffer();
        gl.bindBuffer(gl.ELEMENT_ARRAY_BUFFER, this.indicesBuffer);
        gl.bufferData(gl.ELEMENT_ARRAY_BUFFER, indices, gl.STATIC_DRAW);

        // create the position buffer
        this.InformationsBuffer = gl.createBuffer();
        gl.bindBuffer(gl.ARRAY_BUFFER, this.InformationsBuffer);

        // fill the fresh buffer with data
        gl.bufferData(gl.ARRAY_BUFFER, this.informations, gl.STATIC_DRAW);

        gl.bindBuffer(gl.ARRAY_BUFFER, null);
    }

    updatePositionMatrix() {
        if (!this.needUpdate) { return; }
        mat4.identity(this.modelMatrix);
        this.quaternion = quat.create();
        quat.rotateX(this.quaternion, this.quaternion, this.rotation[0]);
        quat.rotateY(this.quaternion, this.quaternion, this.rotation[1]);
        quat.rotateZ(this.quaternion, this.quaternion, this.rotation[2]);
        mat4.fromRotationTranslationScale(this.modelMatrix, this.quaternion, this.position, this.scale);
        this.needUpdate = false;
    }

    generateInformations(vertices, uvs, normals){

        let y = 0;
        let informations = [];

        if(normals == undefined){
            for (let i = 0; i < vertices.length; i += 3) {
                informations.push(
                    vertices[i],
                    vertices[i + 1],
                    vertices[i + 2],
                    uvs[y],
                    uvs[y + 1]
                )
                y += 2;
            };
        }else{
            for (let i = 0; i < vertices.length; i += 3) {
                informations.push(
                    vertices[i],
                    vertices[i + 1],
                    vertices[i + 2],
                    uvs[y],
                    uvs[y + 1],
                    normals[i],
                    normals[i + 1],
                    normals[i + 2]
                )
                y += 2;
            };
        }


        let informationsArray = new Float32Array(informations);
    
        return informationsArray;
    }
    
    set x(v) { this.position[0] = v; this.needUpdate = true; }
    get x() { return this.position[0]; }
    
    set y(v) { this.position[1] = v; this.needUpdate = true; }
    get y() { return this.position[1]; }
    
    set z(v) { this.position[2] = v; this.needUpdate = true; }
    get z() { return this.position[2]; }

    rotateX(value) { this.rotation[0] = value; this.needUpdate = true; }

    rotateY(value) { this.rotation[1] = value; this.needUpdate = true; }

    rotateZ(value) { this.rotation[2] = value; this.needUpdate = true; }
    
}
export default Geometry;


