import { mat4, quat, vec3} from 'gl-matrix';

class Light {

    constructor(opts = { position: vec3.fromValues(0, 0, 0), color: vec3.fromValues(255, 255, 255) }) {

        this.range = vec3.fromValues(255, 255, 255);
        this.position = vec3.create();
        this.setColor(opts.color);
        this.setPosition(opts.position);

    }

    setColor(vector) {
        this.color = vector;
        vec3.divide(this.color, this.color, this.range);
    }

    setPosition(vector){
        this.position = vector;
    }

    set x(value) { this.position[0] = value; }
    get x() { return this.position[0]; }

    set y(value) { this.position[1] = value; }
    get y() { return this.position[1]; }

    set z(value) { this.position[2] = value; }
    get z() { return this.position[2]; }

    // getNormalizedPosition(){
    //     let x = this.position[0];
    //     let y = this.position[1];
    //     let z = this.position[2];

    //     let long = Math.sqrt(Math.pow(x, 2) + Math.pow(y, 2) + Math.pow(z, 2));
    //     return vec3.fromValues(x/long, y/long, z/long);
    // }
}

export default Light


