class Storage {

  constructor() {
    this.toUpdate = [];
    this.lights = [];
    this.cache = {},
    this.gl,
    this.camera;
  }

  addToUpdate(obj){
    let emptySlotIdx = null;
    for(let i = 0; i < this.toUpdate.length; i++){
      if(emptySlotIdx == null && this.toUpdate[i] == null) emptySlotIdx = i; 
      if(this.toUpdate[i] == obj) return;
    }
    if(emptySlotIdx != null) this.toUpdate[emptySlotIdx] = obj;
    else this.toUpdate.push(obj);
  }

  removeToUpdate(obj){
    for(let i = 0; i < this.toUpdate.length; i++){
      if(this.toUpdate[i] == obj) this.toUpdate[i] = null;
    }
  }
  
  addLight(obj) {
    let emptySlotIdx = null;
    for (let i = 0; i < this.lights.length; i++) {
      if (emptySlotIdx == null && this.lights[i] == null) emptySlotIdx = i;
      if (this.lights[i] == obj) return;
    }
    if (emptySlotIdx != null) this.lights[emptySlotIdx] = obj;
    else this.lights.push(obj);
  }

  removeLight(obj) {
    for (let i = 0; i < this.lights.length; i++) {
      if (this.lights[i] == obj) this.lights[i] = null;
    }
  }
  
}
let StorageInstance = new Storage();
export default StorageInstance;


