import Loaders from '../utils/loader';

class Texture {

    constructor() {

        this.assetsLoaded = false;
        this.constructorDone = false;
        this.readyToUse = false;
        this.loadAssets();
        this.textureUpdated = false;
 
        this.canvas = document.createElement("canvas");
        this.canvas.width = 1024;
        this.canvas.height = 1024;
        this.ctx = this.canvas.getContext("2d");

        this.sideCanvas = document.createElement("canvas");
        this.sideCanvas.width = 1024;
        this.sideCanvas.height = 3 * 1024;
        this.sideCtx = this.sideCanvas.getContext("2d");

        this.delta = 0;

        this.constructorDone = true;
        this.checkStatus();
    }

    loadAssets(){
        Promise.all([
            Loaders.loadImage('../assets/images/lianes.png')
        ]).then((ressources) => {
            this.sprites = ressources[0]; 
            this.assetsLoaded = true;
            this.checkStatus();
        });
    }

    checkStatus(){
        if (this.assetsLoaded == true && this.constructorDone == true) this.init();
    }
    
    init(){
        this.buildImage(0);
        this.ctx.drawImage(this.sideCanvas, 0, 1024, 1024, 1024, 0, 0, 1024, 1024 );

        this.textureUpdated = true;

        this.initEvents();
    }

    buildImage(delta){

        let modulo = 3072;

        this.sideCtx.fillStyle = "rgb(255,255,255)";
        this.sideCtx.fillRect(0, 0, 1024, 3072);
        this.sideCtx.drawImage(this.sprites, 0, 0, 1024, 1024, 0, ((-1024 / 2) + delta) % modulo , 1024, 1024);
        this.sideCtx.drawImage(this.sprites, 0, 0, 1024, 1024, 0, Math.abs( (delta) % modulo), 1024, 1024);
        this.sideCtx.drawImage(this.sprites, 0, 0, 1024, 1024, 0, Math.abs( (delta + (1024 / 2)) % modulo), 1024, 1024);
        this.sideCtx.drawImage(this.sprites, 0, 0, 1024, 1024, 0, Math.abs( ((1024) + delta) % modulo), 1024, 1024);
        this.sideCtx.drawImage(this.sprites, 0, 0, 1024, 1024, 0, Math.abs( ((3 * 1024 / 2) + delta) % modulo), 1024, 1024);
        this.sideCtx.drawImage(this.sprites, 0, 0, 1024, 1024, 0, Math.abs( ((2 * 1024) + delta) % modulo), 1024, 1024);

    }

    initEvents(){

        window.addEventListener('wheel', (e) => {
            this.draw(e.deltaY);
        })

        this.readyToUse = true;
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////

    draw(deltaY){
        this.sideCtx.clearRect(0, 0, this.sideCanvas.width, this.sideCanvas.height);
        this.delta += deltaY;
        if(this.delta < 0) this.delta = (10 * 3072) - this.delta; 
        this.buildImage(this.delta);
        this.ctx.clearRect(0, 0, this.canvas.width, this.canvas.height);
        this.ctx.drawImage(this.sideCanvas, 0, 1024, 1024, 1024, 0, 0, 1024, 1024);
        this.textureUpdated = true;
    }
} export default Texture



