import { vec3 } from 'gl-matrix';
import Background from './components/background.js';
import Monolithe from './components/monolithe.js';
import Rose from './components/rose.js';
import StorageInstance from './storage.js';
import Camera from './classes/camera.js';
import Light from './classes/light.js';
import Stripe from './components/stripe';

class SceneManager {

    constructor(elmt) {
        this.elmt = elmt;
        this.canvas = document.createElement("canvas");

        this.CANVAS_WIDTH = window.innerWidth;
        this.CANVAS_HEIGHT = window.innerHeight;

        this.canvas.width = this.CANVAS_WIDTH;
        this.canvas.height = this.CANVAS_HEIGHT;
        this.context = this.canvas.getContext('webgl', {
            antialias: true
        });

        this.context.getExtension('OES_standard_derivatives');

        this.camera = new Camera();

        StorageInstance.gl = this.context;
        StorageInstance.camera = this.camera;

        StorageInstance.gl.enable(StorageInstance.gl.DEPTH_TEST);

        this.elmt.append(this.canvas);

        this.render = this.render.bind(this);

        this.init();
    }

    init(){
        let background = new Background();
        StorageInstance.addToUpdate(background);

        let monolithe = new Monolithe();
        StorageInstance.addToUpdate(monolithe);

        // let rose = new Rose();
        // StorageInstance.addToUpdate(rose);

        let light = new Light({ position: vec3.fromValues(-0.5, 0.5, 0), color: vec3.fromValues(85, 43, 110) });
        StorageInstance.addLight(light);

        let secondLight = new Light({ position: vec3.fromValues(0.5, -0.5, 0), color: vec3.fromValues(54, 76, 63) });
        StorageInstance.addLight(secondLight);
    
        let firstStripe = new Stripe();
        firstStripe.geometry.z = -15;
        firstStripe.setRelative(-0.52, 0.45, .2, 0.075);
        StorageInstance.addToUpdate(firstStripe);

        let secondStripe = new Stripe();
        secondStripe.geometry.z = -4;
        secondStripe.setRelative(-0.2, -0.45, .42, 0.075);
        StorageInstance.addToUpdate(secondStripe);

        // let thirdStripe = new Stripe();
        // thirdStripe.geometry.z = -4;
        // thirdStripe.setRelative(-0.75, -0.0001, "0.09", .62);
        // StorageInstance.addToUpdate(thirdStripe);

        StorageInstance.time = 0;
        StorageInstance.resolution = [this.CANVAS_WIDTH, this.CANVAS_HEIGHT];

        this.render();
    }

    render(){
        requestAnimationFrame(this.render);
        let gl = StorageInstance.gl;
        StorageInstance.time += 0.05;

        gl.clearColor(0, 0, 0, 0);
        gl.clear(gl.COLOR_BUFFER_BIT);
        gl.viewport(0, 0, this.CANVAS_WIDTH, this.CANVAS_HEIGHT);

        for (const storage of StorageInstance.toUpdate) {
            if (storage.init == true) storage.render();
        }
    }

}
export default SceneManager;


